body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.clue {
  cursor: pointer;
  list-style: none;
  padding: 6px 0px 6px 10px;
}

.selected {
  background: var(--selected-clue-color);
}

.modal-background {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: .8;
  background-color: #666;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.modal-body {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.message {
  background: white;
  padding: 20px;
  border-radius: 5px;
}

.message {
  background: white;
  padding: 20px;
  border-radius: 5px;
}

.example-puzzle {
  list-style: none;
  padding: 5px;
  cursor: pointer;
}

.example-puzzle:hover {
  text-decoration: underline;
}

.cell {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
  text-align: center;
  border: #333 1px solid;
  padding: 2px;
  margin: 0;
  vertical-align: bottom;
  overflow: hidden;

  grid-column-start: auto;
  grid-column-end: auto;
  grid-row-start: auto;
  grid-row-end: auto;

  justify-self: stretch;
  align-self: stretch;
}

.block {
  background-color: #000;
  cursor: default;
}

.number {
  position: absolute;
  top: 2px;
  left: 2px;
  font-size: xx-small;
  text-align: left;
}

/* Props to https://vanseodesign.com/css/vertical-centering/ */
.value {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 50%;
  height: 30%;
  margin: auto;
}

.selected-clue {
  background: var(--selected-clue-color);
}

.input {
  background: var(--selected-input-color);
}

.puzzle {
  border: #111 3px solid;
  border-spacing: 0;
  min-height: 500px;
  margin-top: 20px;

  display: grid;
  grid-gap: 0px;
}

.selected-clue-header {
  display: grid;
  grid-template-columns: auto 1fr;
  -webkit-align-content: center;
          align-content: center;
  grid-gap: 20px 10px;

  background: var(--selected-clue-color);
  padding: 0 10px;
  border-radius: 4px;
  min-height: 60px;
}

.selected-clue-number {
  font-weight: bold;
  -webkit-align-self: center;
          align-self: center;
}

.selected-clue-text {
  -webkit-align-self: center;
          align-self: center;
  margin-left: 5px;
  font-weight: normal;
}

:root {
  --selected-input-color: #96dfcc;
  --selected-clue-color: #e2e2e1;
  --link-color: #69c;
}

.App {
  max-width: 1200px;
  margin: auto;
  padding: 0 10px;
}

.puzzle-area {
  display: grid;
  grid-template-columns: [puzzle] 2fr [across] 1fr [down] 1fr;
  grid-gap: 10px;
}

.puzzle-container {
  grid-column: puzzle;
}

.across{
  grid-column: across;
}

.down{
  grid-column: down;
}

@media (max-width: 700px) {
  .puzzle-area {
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "puzzle puzzle"
      "across down";
  }
}

.clues {
  margin: 0;
  padding: 0;
  max-height: 500px;
  overflow: scroll;
}

.date-link {
  color: #69c;
  color: var(--link-color);
  display: inline;
  cursor: pointer;
}

.date-link:hover {
  text-decoration: underline;
}

